import React from 'react';

import {Col, Row} from "reactstrap";

import {LargeTextGray, LargeText, SmallText, SmallTextGray} from "./Text";
import {DataContextConsumer} from "../DataContext";


type EducationProps = {
    title: string
    text: string
    dates: string
    duration: string
}

class Experience extends React.Component<EducationProps> {
    render() {
        return (
            <Row className="mb-4">
                <Col xs="3" className="text-right" style={{"color": "#B0B0B0", width: "10em"}}>
                    <LargeTextGray className="m-0">&nbsp;{this.props.dates}</LargeTextGray>
                    <SmallTextGray>{this.props.duration}</SmallTextGray>
                </Col>
                <Col>
                    <LargeText className='mb-2'>{this.props.title}</LargeText>
                    {this.props.text.split('\n').map((line, index) =>
                        line ? <SmallText key={index.toString()}>{line}</SmallText> :
                            <br key={index.toString()}/>
                    )}
                </Col>
            </Row>
        );
    }
}

function Experiences() {
    return (
        <DataContextConsumer>
            {({data}) =>
                <React.Fragment>
                    {
                        data.experience.map((item, index) =>
                            <Experience
                                key={index.toString()}
                                title={item.title}
                                text={item.text}
                                dates={item.dates}
                                duration={item.duration}
                            />
                        )
                    }
                </React.Fragment>
            }
        </DataContextConsumer>
    );
}

export default Experiences;
