import React from 'react';

import {Col, Media, Row} from "reactstrap";

import {SmallText, LargeTextGray, XLargeText} from "./Text";
import {EmailIcon, LinkedInIcon, InstagramIcon} from "./SocialLink";
import {DataContextConsumer} from "../DataContext";


function HeaderAbout() {
    return (
        <DataContextConsumer>
            {({data}) =>
                <Col>
                    <Row className="justify-content-center text-center">
                        <Col xs="12" sm="auto" className="pl-sm-0">
                            <Media object src="avatar.png" alt="Avatar" style={{maxWidth: '160px'}}/>
                        </Col>
                        <Col>
                            <Row name="title">
                                <XLargeText className="mx-auto mx-sm-0">{data.common.name}</XLargeText>
                            </Row>

                            <Row name="subtitle">
                                <LargeTextGray
                                    className="mx-auto mx-sm-0 mb-0 mb-sm-2">{data.common.position}</LargeTextGray>
                            </Row>

                            <Row className="d-sm-block d-none mt-4 pr-1">
                                <SmallText>{data.common.about}</SmallText>
                            </Row>

                            <Row name="links" className="mt-0 mt-sm-1 justify-content-center justify-content-sm-start">
                                <Col xs="auto" className="px-0 pb-1">
                                    <LinkedInIcon/>
                                </Col>
                                <Col xs="auto" className="px-1 pb-1">
                                    <EmailIcon/>
                                </Col>
                                <Col xs="auto" className="px-0 pb-1">
                                    <InstagramIcon/>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row className="align-self-end d-block d-sm-none mb-3 mt-4">
                        <SmallText>{data.common.about}</SmallText>
                    </Row>
                </Col>
            }
        </DataContextConsumer>
    );
}

export default HeaderAbout;
