import React from 'react';

import {Badge, Col, Progress, Row} from "reactstrap";

import {LargeText, SmallText} from "./Text";
import styled from "styled-components";
import {DataContextConsumer} from "../DataContext";

const OtherProgress = styled(Progress)`
background: linear-gradient(90deg, rgba(86,175,175,0) 0%, rgb(86,175,175) 20%, rgb(148,227,148) 40%, rgb(249,248,113) 80%, rgba(249,248,113,0) 100%);
border-radius: 0;
`

const StyledProgressBack = styled(Progress)`
background-color: #E8E8E8;
z-index: 1;
border-radius: 0 10px 10px 0;
`
const StyledProgress = styled(Progress)`
background: linear-gradient(90deg, #56AFAF 0%, #6FD4A4 100%);
z-index: 3;
border-radius: 0 10px 10px 0;
`

const StyledBadge = styled(Badge)`
background-color: #E8E8E8;
color: #666666;
font-size: small;
font-weight: 100;
`

type SkillProps = {
    title?: string
    percent?: string
    text: string
    tags: string[]
    className: string
}

class Skill extends React.Component<SkillProps> {
    render() {
        return (
            <Col md="6" className={this.props.className}>
                {
                    this.props.title &&
                    <LargeText>{this.props.title}</LargeText>
                }
                {
                    this.props.percent && this.props.percent === "100" &&
                    <OtherProgress/>
                }
                {
                    this.props.percent && this.props.percent !== "100" &&
                    <StyledProgressBack multi>
                        <StyledProgress bar value={this.props.percent} z_index="3"/>
                    </StyledProgressBack>
                }
                <SmallText className='mt-lg-3 mb-1'>
                    {this.props.text}
                </SmallText>
                {
                    this.props.tags.map((tag, index) =>
                        <StyledBadge key={index.toString()} className="mr-1" pill>{tag}</StyledBadge>
                    )
                }
            </Col>
        );
    }
}

function Skills() {
    return (
        <DataContextConsumer>
            {data =>
                <Row>
                    {
                        data.data.skills.map((skill, index) =>
                            <Skill className="mb-5"
                                   key={index.toString()}
                                   title={skill.name}
                                   text={skill.text}
                                   tags={skill.tags}
                                   percent={skill.level}
                            />
                        )
                    }
                </Row>
            }
        </DataContextConsumer>
    );
}

export default Skills;
