import React from 'react';

import {Col, Row} from "reactstrap";

import {LargeText, SmallTextBlack, LargeTextGray, SmallText, SmallTextGray} from "./Text";
import styled from "styled-components";
import {DataContextConsumer} from "../DataContext";
import {ILink} from "../data/content";


const StyledLink = styled.a`
font-size: small;
font-weight: bold;
margin-right: 1rem;
color: #B0B0B0;
text-decoration: underline;

:hover {
color: #56AFAF;
}
`

type EducationProps = {
    title: string
    subtitle: string
    text: string
    links: ILink[]
    dates: string
    duration: string
}

class Education extends React.Component<EducationProps> {
    render() {
        return (
            <Row className="mb-4">
                <Col xs="3" className="text-right" style={{"color": "#B0B0B0", width: "10em"}}>
                    <LargeTextGray className="m-0">{this.props.dates}</LargeTextGray>
                    <SmallTextGray>{this.props.duration}</SmallTextGray>
                </Col>
                <Col>
                    <LargeText className="m-0">{this.props.title}</LargeText>
                    <SmallTextBlack className="mb-3">{this.props.subtitle}</SmallTextBlack>
                    <SmallText>{this.props.text}</SmallText>
                    {
                        this.props.links.map((link, index) =>
                            <StyledLink key={index.toString()} href={link.link} target="_blank"
                                        rel="noopener noreferrer">
                                {link.name}
                            </StyledLink>
                        )
                    }

                </Col>
            </Row>
        );
    }
}

function Educations() {
    return (
        <DataContextConsumer>
            {({data}) =>
                <div>
                    {
                        data.education.map((item, index) =>
                            <Education
                                key={index.toString()}
                                title={item.name}
                                subtitle={item.degree}
                                text={item.text}
                                links={item.links}
                                dates={item.dates}
                                duration={item.duration}
                            />
                        )
                    }
                </div>
            }
        </DataContextConsumer>
    );
}

export default Educations;
