import React, {Component} from "react";

import {IData, init_locale, JsonData} from "./data/content";

const {Provider, Consumer} = React.createContext({data: JsonData(), locale: 'en'});


interface IProps {
    children: object
}

interface IState {
    data: IData
    locale: string
}

class DataContextProvider extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        let locale = init_locale();

        this.state = {
            data: JsonData(),
            locale: locale,
        };
    }

    render() {
        return (
            <Provider value={this.state}>
                {this.props.children}
            </Provider>
        );
    }
}

export {DataContextProvider, Consumer as DataContextConsumer};
