import React from 'react';

import {Col, Row} from "reactstrap";
import {DataContextConsumer} from "../DataContext";
import styled from "styled-components";

const StyledSpan = styled.a`
font-size: x-small;
color: #3a3a3a;
text-decoration: underline;
cursor: pointer;

:hover {
color: #56AFAF;
}
`

function Footer() {
    return (
        <DataContextConsumer>
            {({data, locale}) =>
                <Col>
                    <Row className="text-right">
                        <Col className="p-0">
                            {
                                locale === 'en' &&
                                <React.Fragment>
                                    <StyledSpan href='/?lang=ru' rel="noopener">ru</StyledSpan>
                                    <span style={{
                                        color: "#3a3a3a",
                                        fontSize: "x-small",
                                    }}> / </span>
                                    <span style={{
                                        color: "#B0B0B0",
                                        fontSize: "x-small",
                                        fontWeight: 900
                                    }}>en</span>
                                </React.Fragment>
                            }
                            {
                                locale === 'ru' &&
                                <React.Fragment>
                                    <span
                                        style={{
                                            color: "#B0B0B0",
                                            fontSize: "x-small",
                                            fontWeight: 900
                                        }}>ru</span>
                                    <span style={{
                                        color: "#3a3a3a",
                                        fontSize: "x-small",
                                    }}> / </span>
                                    <StyledSpan href='/?lang=en' rel="noopener">en</StyledSpan>
                                </React.Fragment>
                            }
                        </Col>
                    </Row>
                    <Row className="pb-1">
                        <Col>
                            <footer className="text-center" style={{color: "#3a3a3a", fontSize: "x-small"}}>
                                {data.copyright}
                            </footer>
                        </Col>
                    </Row>
                </Col>
            }
        </DataContextConsumer>
    );
}

export default Footer;
